<template>
  <div id="add_cash">
    <div>
      <router-link to="/home">查看账单</router-link>
    </div>
    <div>
      <span :class="type?'bg_green':'bg_red'" @click="getBilltype(0)">支出</span>
      <span :class="type?'bg_red':'bg_green'" @click="getBilltype(1)">收入</span>
    </div>
    <div>
      金额：<input type="text" placeholder="请输入金额" v-model="amount"/>
    </div>
    <div @click="showCgslist">
      <span>类别：</span><span> {{ name }} </span>
      <span><img :src="imgsrc" alt="图片"></span>
    </div>

    <div>
      <span>时间：</span><span> {{ time }} </span>
    </div>
    <div>
      <button class="bg_red btn" @click="saveBill">保存</button>
    </div>

    <div>
      <div @click="getName($event)" v-show="is_showcgslist">
        <div v-for=" (item,index) in cgs_list" :key="index" :data-index="index">
          {{ item[2] }}
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data(){
    return {
      cgs_list:this.$store.getters.getGdata.cgs.slice(1),
      imgsrc:require('../assets/left.png'),
      amount:'',
      add_time:0,
      time:'',
      type:1,
      name:'请选择交易类型',
      is_showcgslist:false
    }
  },
  created(){
    //获取当前时间以及类别初始化
    this.initTimename();
  },
  methods:{
    getBilltype(type){
      this.type = type;
    },

    initTimename(){
      this.name = this.cgs_list[0][2];
      this.add_time = new Date().getTime();

      this.time = this.commonFunc.timeFormat(this.add_time);
    },
    showCgslist(){
      console.log('调出分类列表');
      this.is_showcgslist=!this.is_showcgslist;
    },

    amtTest(){
      //1、非空判断
      //2、不以-，+开头
      //3、只含数字，且不为0，最多两位小数
      if(this.amount){
        if(this.amount[0]!=="-" && this.amount[0]!=="+"){
          console.log(this.amount);
          var x = -(-this.amount);
          var reg = /^\d+(\.\d{1,2})?$/g;
          if(reg.test(x) && x){
            console.log('格式正确');
            this.amount = x;
            return this.amount;
          }else{
            alert('请输入大于零数字，最多保留两位小数')
          }
        }else{
          // console.log('请不要以-,+开头')
          alert('请输入大于零数字，最多保留两位小数');
        }
      }else{
        //非空判断
        alert("请输入大于零数字，最多保留两位小数")
      }
      
    },
    
    //获得账单类别
    getName(e){
      if(e.target.dataset.index){
        var i = parseInt(e.target.dataset.index);
        this.name = this.cgs_list[i][2];
        console.log(this.name);
        //收起列表
        this.is_showcgslist=!this.is_showcgslist
      }
    },

    //保存账单
    saveBill(){
      //先判断金额
      if(this.amtTest()){
        console.log("输入的金额正确");
        var new_bill={
          'type':this.type,
          'time':this.add_time,
          'name':this.name,
          'amount':this.amount
          };
        console.log(new_bill);
        var new_data = this.$store.getters.getGdata.bills;
        new_data = new_data.push(new_bill);
        alert('保存成功');

        //更新时间，金额，类别
        this.initTimename();
        this.amount = '';
      }
    }

  }
}
</script>

